.services {
    // #208100
    &__head {
        h3 {
            font-family: serif;
            span {
                color: rgb(1, 132, 117);
                font-size: 18px;
                font-family: 'Courier New', Courier, monospace;
            }
        }
    }
    &__box {
        .carts {
            background-color: rgb(1, 132, 117);
            padding: 10px;
            color: white;
            border-radius: 6px;
            &__heading {
                text-align: center;
                i {
                    font-size: 30px;
                }
                h6 {
                    font-family: cursive;
                }
            }
            &__content {
                img {
                    width: 100%;
                    opacity: 0.7;
                    height: 180px;
                    border-radius: 20px;
                    object-fit: cover;
                    padding: 10px 0px 10px 0px;
                }
            }
        }
    }
}

//Секция о нас старт
.about {
    h3 {
        font-family: serif;
        span {
            color: rgb(1, 132, 117);
            font-size: 18px;
            font-family: 'Courier New', Courier, monospace;
        }
    }
}

.nihad {
    hr {
        color: rgb(1, 132, 117);
    }
    text-align: center;
    &__photos {
        img {
            border-radius: 200px;
            width: 250px;
        }
    }
    &__buttons {
        a {
            background-color: rgb(1, 132, 117);
            color: white;
            transition: .2s ease-in-out;
            border-radius: 4px;
            padding: 10px;
            text-decoration: none;
            &:hover {
                color: rgb(1, 132, 117);
                background-color: white;
                border: 2px solid rgb(1, 132, 117);
            }
        }
    }
}

.huseyn {
    text-align: center;
    &__photos {
        img {
            border-radius: 200px;
            width: 236.5px;
        }
    }
    &__buttons {
        a {
            background-color: rgb(1, 132, 117);
            color: white;
            transition: .2s ease-in-out;
            border-radius: 4px;
            padding: 10px;
            text-decoration: none;
            &:hover {
                color: rgb(1, 132, 117);
                background-color: white;
                border: 2px solid rgb(1, 132, 117);
            }
        }
    }
    hr {
        color: rgb(1, 132, 117);
    }
}

// consulting csss
.consulting {
    &__contents {
        box-shadow: 1px 1px 1px 3px rgb(1, 132, 117);
        border: 1px solid rgb(1, 132, 117);
        padding: 15px;
        transition: .2s ease-out;
        border-radius: 6px;
        &:hover {
            transform: scale(0.9);
            opacity: 0.8;
        }
    }
    &__buttons {
        a {
            background-color: rgb(1, 132, 117);
            color: white;
            transition: .2s ease-in-out;
            border-radius: 4px;
            padding: 10px;
            text-decoration: none;
            &:hover {
                color: rgb(1, 132, 117);
                background-color: white;
                border: 2px solid rgb(1, 132, 117);
            }
        }
    }
}